.page1bg {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
}
.Tilt {
	z-index: 100000000000;
}
.Page1Content {
	width: 50vw;
	height: 60vh;

	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
	opacity: 0;
	transform: scale(0);
	transition: all 500ms;
	transition: all 500ms;

	background: rgba(255, 255, 255, 0.027) !important;
	box-shadow: 3px 1px 38px -12px #00000094 !important;
	border-radius: 28px !important;
	backdrop-filter: blur(50px) !important;
	-webkit-backdrop-filter: blur(20px);
}
.fadeIn {
	opacity: 1;
	transform: scale(1);
	transition: all 500ms;
	transition: all 500ms;
}
.fadeOut {
	opacity: 0;
	transform: scale(0);
	transition: all 500ms;
	transition: all 500ms;
}
.left,
.right {
	width: 50%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.equinoxlogo {
	width: 80%;
	/* height: 245px; */
	-webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
	        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
