#devfolio-apply-now {
	align-items: center;
	outline: none;
	background-color: #3770ff;
	border-radius: 3px;
	border: none;
	color: #ffffff;
	cursor: pointer;
	display: flex;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
		sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
	font-size: 18px;
	font-weight: 600;
	padding: 12px 20px;
	text-align: center;
	cursor: pointer;
}

#devfolio-apply-now:not([disabled]) {
	cursor: pointer;
}

#devfolio-apply-now:hover {
	background-color: #2954bf !important;
}
.regNow {
	cursor: pointer;
	font-family: Roboto;
	font-style: normal;
	font-weight: 900;
	font-size: 120%;
	color: #ffffff;
	background-color: #3770ff;
	width: 50%;
	height: 7%;
	border-radius: 15px;
	display: flex;
	justify-content: center;
	align-items: center;
	transform: scale(1.2);
}
.links {
	display: flex;
	margin-top: 5%;
	width: 50%;
	align-items: center;
	justify-content: space-between;
}
.linklogos {
	margin: 2%;
	cursor: pointer;
	color: white;
	/* transform: scale(1.8); */
	font-size: 200%;
	transition: all 0.5s;
}
.at {
	width: 13%;
}
.linklogos:hover {
	transform: scale(1.2);
	transition: all 0.5s;
}
.contact {
	width: 100%;
	opacity: 0;
	font-family: 'roboto';
	font-style: normal;
	color: white;
	transition: all 0.3s;
	text-align: center;
}
.showContact {
	width: 100%;
	height: auto;
	opacity: 1;
	text-align: center;
	transition: all 0.3s;
}
.applyimg {
	cursor: pointer;
	-webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
	        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
	transition: all 0.3s;
	width: 60%;
}
.applyimg:hover {
	-webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.55));
	        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.55));
	transition: all 0.3s;
}

